@import url('https://fonts.googleapis.com/css?family=Raleway');

body{
    background-color: rgba(	1, 65, 131,.3);
}

.center{
    font-family: 'Raleway', sans-serif;
    background-color: transparent;
    position: absolute;
    height: 100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.card{
    -webkit-box-shadow: 0rem .5rem 1rem rgba(0,0,0,.3);
            box-shadow: 0rem .5rem 1rem rgba(0,0,0,.3);
    background-color: #fff;
    border-radius: 15px;
    padding: 0.8rem;
    align: center
}
img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
.card > form{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.card h1{
    text-align: center;
    margin-top: 0;
    margin-bottom: 10px;
}

.form-item{
    font-family: 'Raleway', sans-serif;
    padding: 5px;
    margin-bottom: 2rem;
    height: 30px;
    width: 16rem;
    border: 1px solid grey;
}

.form-submit{
    font-family: 'Raleway', sans-serif;
    height: 35px;
    color: #fff;
    background-color: red;
    border: none;
    letter-spacing: 0.2rem;
    -webkit-transition: 0.3s opacity ease;
    -o-transition: 0.3s opacity ease;
    transition: 0.3s opacity ease;
    cursor: pointer;
}
.form-submit:hover{
   opacity: 0.6;
}

/* The only rule that matters */
#video-background {
    /*  making the video fullscreen  */
      position: fixed;
      right: 0;
      bottom: 0;
      z-index: -100;
    }


